*{
    margin:0;
    padding:0;
    box-sizing: border-box;
}
.item{
    display: flex;
    min-width: 300px;
    width: 20vw;
    height: 350px;
    margin: 5px;
    border: 1px solid rgb(65, 216, 171);
    box-shadow: 5px 5px 7px 0px rgba(109, 104, 104, 0.73);
    border-radius: 10px;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}
.figura {
    position: relative;
    width: 20vw;
    height: 350px;
    min-width: 300px;
    overflow: hidden;
    border-radius: 6px;
    box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.5);
    cursor: pointer;
  }
  .imagen {
    width: 100%;
    height: 100%;
    min-width: 300px;
    transition: all 500ms ease-out;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .imagen h3{
      margin-bottom: 5px;
      font-family: monospace;
      font-size: x-large;
  }
  .capa {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(101, 202, 253, 0.7);
    transition: all 500ms ease-out;
    opacity: 0;
    visibility: hidden;
    text-align: center;
  }
  .figura:hover > .capa {
    opacity: 1;
    visibility: visible;
  }
  .figura:hover > .imagen {
    transform: scale(1.1);
  }